import {
  getProfessionAttach,
  deleteAttachInfo
} from "../../../../../utils/employment.url";
import attachDialog from "./commonModel/attachDialog.vue";
export default {
  components: {
    attachDialog
  },
  data() {
    return {
      input: '',
      attachList: [], //院系列表
      search: '',
      pageObj: {
        pageRowNum: 8,
        currPage: 1,
        totalRow: 20,
      },
      dialogFormVisible: false,
      editAttachInfo: {},
      defaultFormData: {
        session: "",
        departmentKey: '',
        professionKey: "",
        classKey: ""
      },
      formdata: {
        session: "",
        departmentKey: '',
        professionKey: "",
        classKey: ""
      },
    }
  },
  methods: {
    getAttachList(session, departmentKey, professionKey, classKey) {
      var params = {
        session: session ? session : "",
        departmentKey: departmentKey ? departmentKey : "",
        professionKey: professionKey ? professionKey : "",
        classKey: classKey ? classKey : "",
        currPage: this.pageObj.currPage || "",
        pageRowNum: this.pageObj.pageRowNum || "",
        totalRow: this.pageObj.totalRow || ""
      };
      getProfessionAttach(params, "get").then(res => {
        if (res.status == 200) {
          this.attachList = res.data.attachList;
          this.pageObj = res.data.pageObj;
          // this.attachList = this.groupBy(this.attachList, function(item) {
          //   return [item.departmentKey, item.departmentName]; 
          // });
        }
      });
    },
    changePage(val) {
      console.log(val)
      this.pageObj.currPage = val;
      this.getAttachList();
    },
    dialogClose(value) {
      this.$store.commit('changeAttachStatus', false);
      this.dialogFormVisible = value;
      this.getAttachList();
    },
    handleEdit(index, row) {
      // console.log(row)
      this.editAttachInfo = row;
      this.dialogFormVisible = true;
      this.$store.commit('changeAttachStatus', true);
    },
    handleDelete(index, row) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteAttachInfo(row, "delete").then(res => {
          if (res.status == 200) {
            this.getAttachList();
            this.$message({
              message: res.message,
              type: "success"
            });
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    //数组对象分组
    groupBy(array, f) {
      var groups = {};
      array.forEach(function (o) {
        var group = JSON.stringify(f(o));
        groups[group] = groups[group] || [];
        groups[group].push(o);
      });
      return Object.keys(groups).map(function (group) {
        var _group = JSON.parse(group)
        return {
          key: _group[0],
          name: _group[1],
          child: groups[group]
        };
      });
    },
    //搜索
    submitForm(formdata) {
      this.pageObj.pageRowNum=8;
      this.pageObj.currPage=1;
      this.getAttachList(formdata.session, formdata.departmentKey, formdata.professionKey, formdata.classKey, )
    },
    //重置
    resetForm() {
      this.formdata = Object.assign({}, this.defaultFormData);
      this.getAttachList();
    },
    addNewAttach() {
      this.dialogFormVisible = true;
      this.editAttachInfo = {};
      this.$store.commit('changeAttachStatus', false);
    }
  },
  created() {
    this.getAttachList();
  }
}