<template>
  <div class="attachDialogBox">
    <el-dialog
      :title="this.$store.getters.getAttachFlag ? '修改班级信息' : '新增新班级'"
      :visible="dialogFormVisible"
      v-if="dialogFormVisible"
      width="40%"
      @close="dialogClose"
    >
      <el-form :model="attachForm" :rules="rules" ref="attachForm">
        <el-form-item label="所属年份" :label-width="formLabelWidth" prop="attachId">
          <el-date-picker
            v-model="attachForm.session"
            type="year"
            placeholder="选择所属年级"
            value-format="yyyy"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="院系名称" :label-width="formLabelWidth" prop="departmentName">
          <el-input v-model="attachForm.departmentName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="院系代码" :label-width="formLabelWidth" prop="departmentKey">
          <el-input v-model="attachForm.departmentKey" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="专业名称" :label-width="formLabelWidth" prop="professionName">
          <el-input v-model="attachForm.professionName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="专业代码" :label-width="formLabelWidth" prop="professionKey">
          <el-input v-model="attachForm.professionKey" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="班级名称" :label-width="formLabelWidth" prop="className">
          <el-input v-model="attachForm.className" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="班级代码" :label-width="formLabelWidth" prop="classKey">
          <el-input v-model="attachForm.classKey" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelEdit">取消</el-button>
        <el-button type="primary" @click="submitForm('attachForm')">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {
  addNewAttachInfo,
  updateAttachInfo
} from "../../../../../../utils/employment.url";
import { isNum } from "@/utils/utils";
export default {
  props: ["dialogFormVisible", "attachForm"],
  data() {
    return {
      // presentTitle:"添加就业代表信息",
      formLabelWidth: "120px",
      fileList: [],
      rules: {
        session: [
          { required: true, message: "请选择所属年份", trigger: "blur" }
        ],
        departmentName: [
          { required: true, message: "请输入院系名称", trigger: "blur" }
        ],
        departmentKey: [
          { required: true, message: "请输入院系代码", trigger: "blur" }
        ],
        professionName: [
          { required: true, message: "请输入专业名称", trigger: "blur" }
        ],
        professionKey: [
          { required: true, message: "请输入专业代号", trigger: "blur" }
        ],
        className: [
          { required: true, message: "请输入专业名称", trigger: "blur" }
        ],
        classKey: [
          { required: true, message: "请输入专业代号", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    dialogClose() {
      this.$emit("dialogClose", false);
    },
    //,取消
    cancelEdit() {
      this.$emit("dialogClose", false);
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.$store.getters.getAttachFlag) {
            updateAttachInfo(this.attachForm, "put").then(res => {
              if (res.status == 200) {
                this.$message({
                  message: res.message,
                  type: "success"
                });
                this.$emit("dialogClose", false);
              }
            });
          } else {
            addNewAttachInfo(this.attachForm, "post").then(res => {
              if (res.status == 200) {
                this.$message({
                  message: res.message,
                  type: "success"
                });
                this.$emit("dialogClose", false);
              }
            });
          }
        } else {
          return false;
        }
      });
    }
  },
  created() {}
};
</script>
<style lang="scss" scoped>
.attachDialogBox {
  .el-dialog {
    .el-form {
      .el-form-item {
        .el-input {
          width: 300px;
        }
      }
    }
  }
}
</style>